<template>
  	<div>
        <section class="user_info">
            <img v-if="!headUrl" src="../assets/headUrl.jpg">
            <img v-else :src="headUrl">
            <span class="user_name">{{userNick}}</span>
        </section>
        <section class="operate_list">
            <ul>
                <li><router-link to="/mystore"><i class="i-store"></i><span></span><em class="iconfont icon-right"></em></router-link></li>
                <li><router-link to="/myupload"><i class="i-upload"></i><span></span><em class="iconfont icon-right"></em></router-link></li>     
            </ul>
            <ul>
                <li><router-link to="/awards"><i class="i-award"></i><span></span><em class="iconfont icon-right"></em></router-link></li>
                <li v-if="invite"><a href="http://www.571xz.com/h5/recommendVip.htm"><i class="i-invite"></i><span></span><em class="iconfont icon-right"></em></a></li>
            </ul>
            <ul>
                <li><router-link to="/settings"><i class="i-setting"></i><span></span><em class="iconfont icon-right"></em></router-link></li>
            </ul>
        </section>

        <common-foot activeId="usercenter"></common-foot>
    </div>
</template>


<script>
import commonFoot from '../components/commonFoot'
import {getUserInfo, showInvite, ajax} from '../data/service'

export default {
    data: function(){
    	return{
            headUrl:'',
            userNick:'',
            invite: true
    	}
    },
    created(){
        this.getUserInfo(),
        this.showInvite()
    },
    mixins:[ajax],
    methods:{
        async getUserInfo(){
            this.ajax(await getUserInfo(), function(resp){
                this.headUrl = resp.headUrl
                this.userNick = resp.userNick  
            })
        },
        async showInvite(){
            this.ajax(await showInvite(), function(resp){
                this.invite = resp.success
            })
        }
    },
    components:{
        commonFoot
    }
}
</script>

<style scoped>
.user_info{width:100%; height:2.1rem; background:url(../assets/userbg.png) no-repeat 0 0; background-size:100% 100%; text-align:center;}
.user_info img{width:.9rem; height:.9rem; border-radius:50% 50%; margin-top:.48rem; border:.05rem solid rgba(255,255,255,.2);}
.user_info span{display:block; font-size:.16rem; color:#fff; line-height:.3rem;}

.operate_list{margin-top:.3rem; padding-bottom:.6rem;}
.operate_list ul{margin-top:.15rem; background:#fff; padding:0 .14rem; border:1px solid #d8d8d8; border-width:1px 0;}
.operate_list li{border-bottom:1px solid #d8d8d8; padding:.1rem 0; font-size:.16rem; color:#333; overflow:hidden;}
.operate_list li a{display:block;}
.operate_list li i{float:left; display:block; width:.24rem; height:.22rem; margin-right:.1rem; background-repeat:no-repeat; background-position:center center; background-size:100% auto; position:relative; top:.04rem;}
.operate_list li span{line-height:.32rem;}
.operate_list li em{float:right; color:#d8d8d8; font-size:.2rem; position:relative; top:.03rem; right:-.04rem;}
.operate_list ul li:last-child{border-bottom:0;}
.operate_list li i.i-store{background-image:url(../assets/istore.png); }
.operate_list li i.i-upload{background-image:url(../assets/iupload.png); }
.operate_list li i.i-award{background-image:url(../assets/iaward.png); }
.operate_list li i.i-invite{background-image:url(../assets/iinvite.png);}
.operate_list li i.i-setting{background-image:url(../assets/isetting.png); top:.05rem;}

</style>



