<template>
	<div class="appTipBar" v-if="showTip">
		<span @click="closeTipBar"></span>
		<a href="http://www.571xz.com/static/NewDown/jinhuobao.html"></a>
	</div>
</template>

<script>
export default{
	data(){
		return {	
			showTip: true		
		}
	},
	methods:{
		closeTipBar(){
			this.showTip = false;
		}
	}
}
</script>

<style scoped>
.appTipBar{position:fixed; left:0; top:0; z-index:10000; width:100%; height:.7rem; background:url(../assets/tipBar.png) no-repeat 0 0; background-size:100% 100%;overflow:hidden;}
.appTipBar span{display:block; width:.4rem; height:.28rem; margin-top:.28rem; float:left;}
.appTipBar a{display:block;float:right; width:3.35rem; height:.7rem;}
</style>


