<template>
  	<div>
        <common-head :view="{
            goback: true,
            pageTitle: '',
            togglePageType: true,
        }"></common-head>
		
		<section class="goods_menu">
            <div class="swiper-container" id="cateSwiper">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="item in catMenu" :class="{current: curPid == item.id}">
                        <span  @click="changePid(item.id)">{{item.name}}</span>
                    </div>
                </div>
            </div>
		</section>
    
        <section class="goods-list"><goods-list :goods="goodsList"></goods-list></section>
        
        <div v-show="hasNext" @click="nextPage" class="next_page"><span></span></div>
        
        <loading></loading>
    </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import goodsList from '../components/goodsImgList'
import Loading  from '../components/loading'
import {getNewGoodsList, getGoodsCategory, ajax} from '../data/service'

export default {
    name: 'newgoods',
    components:{
    	commonHead,
        goodsList,
        Loading,
    },
    data: function(){
    	return{
            catMenu: [],
            curPid: 30,
            goodsList: [],
            pageIndex: 1,
            hasNext: false,
            rerender: false,
            type: 1
    	}
    },
    created(){
        this.queryCatMenu()
        this.queryGoodsList()
        
    },
    mixins: [ajax],
    methods:{
        ...mapActions(['startLoding']),
        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
        async queryCatMenu(){
            this.loadingShow()

            this.ajax(await getGoodsCategory(this.website, this.pid == 30 ? 1 : 2), (resp) => {
                this.catMenu = resp.newgoodsCats
                setTimeout(function(){
                    new Swiper('#cateSwiper', {
                        slidesPerView: 5,
                        slideToClickedSlide:true
                    })
                })
            })
            this.loadingHide()
        },
        async queryGoodsList(){
            this.loadingShow()

            let options = {
                webSite: this.website,   
                pid: this.curPid,
                index: this.pageIndex,
                size: 30
            }
            this.ajax(await getNewGoodsList(options), (resp) => {
                if(this.rerender){
                    this.rerender = false
                    this.goodsList = resp.goodsList
                }else{
                    this.goodsList = this.goodsList.concat(resp.goodsList)
                }
                this.hasNext = resp.hasNext
            })

            this.loadingHide()
        },
        nextPage(){
            this.pageIndex++
            this.queryGoodsList()
        },
        changePid(pid){
            if(pid != this.curPid){
                this.curPid = pid
                this.pageIndex = 1
                this.rerender = true
                this.queryGoodsList()
            }
        },
    },
    computed: {
        ...mapState({
            'pid': state => state.pid,
            'website': state => state.website,
            'loading': state => state.loading,
        }),
    },
    watch: {
        pid(){
            this.changePid(this.pid)
            this.queryCatMenu()
        },
    }
}
</script>

<style scoped>
.goods_menu{width:100%; height:.48rem; padding-top:.04rem; background:#fff; position:fixed; top:.46rem; left:0; box-shadow:0 1px .06rem rgba(0,0,0,.1);}
.goods_menu .swiper-wrapper{display:flex;}
.goods_menu .swiper-slide{width:.75rem; font-size:.15rem; text-align:center;}
.goods_menu .swiper-slide span{display:inline-block; padding:0 .08rem; height:.44rem; line-height:.44rem; border-bottom:.02rem solid #fff;}
.goods_menu .current span{border-color:#333;}

.goods-list{margin-top:.08rem; padding-top:.92rem; overflow:hidden;}
</style> 




